import { cn } from "@/lib/utils";

export function Card({
  className,
  children,
}: {
  className?: string;
  children: React.ReactNode;
}) {
  return (
    <div className={cn("card-glass rounded-3xl p-6", className)}>{children}</div>
  );
}

export function CardHeading({ title, kicker }: { title: string; kicker?: string }) {
  return (
    <div className="mb-4">
      {kicker && (
        <span className="uppercase tracking-[0.3em] text-[12px] text-slate-500">{kicker}</span>
      )}
      <h3 className="mt-2 text-2xl font-semibold text-slate-900">{title}</h3>
    </div>
  );
}
